@import url(https://fonts.googleapis.com/css?family=Merriweather:100,200,300,300i,400,400i,700);
@import url(https://fonts.googleapis.com/css?family=Roboto+Slab:400,700);
@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400);
.titles__title {
  font-family: "Roboto Slab", serif;
  font-size: 50px;
  line-height: 1.3;
  letter-spacing: 2px; }

.titles__subtitle {
  font-family: "Merriweather", serif;
  font-style: italic;
  font-weight: 100;
  line-height: 1.5;
  letter-spacing: 1px; }

form input[type="text"] {
  width: 30%;
  margin-right: 20px;
  margin-bottom: 30px;
  padding-bottom: 4px;
  color: #fff !important;
  font-size: 20px;
  font-weight: lighter;
  letter-spacing: 2px;
  background-color: transparent;
  border: 0;
  border-bottom: solid 1px #f16051;
  outline: none; }

form input:-webkit-autofill {
  -webkit-box-shadow: 0 0 0 30px #2c3e50 inset;
  -webkit-text-fill-color: #fff !important; }

form button {
  margin: 0 2px;
  padding: 8px 20px;
  color: #fff;
  font-size: 15px;
  font-weight: 100;
  letter-spacing: 1px;
  background-color: #f16051;
  border: 0;
  border-radius: 2px;
  cursor: pointer; }
  form button:active {
    outline: none; }

.weather {
  width: 60%;
  font-size: 20px;
  font-weight: 200;
  letter-spacing: 2px; }
  .weather__key {
    padding: 20px 0 20px 0;
    color: #f16051;
    font-weight: 400;
    border-bottom: solid 2px rgba(255, 255, 255, 0.06); }
    .weather__key:last-child {
      border: 0; }
  .weather__value {
    color: #fff;
    font-weight: 200; }
  .weather__error {
    color: #f16051;
    font-size: 20px;
    font-weight: 200;
    letter-spacing: 1px; }

body {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
  margin: 0;
  background: -webkit-linear-gradient(left, #e67e22, #e74c3c);
  background: linear-gradient(to right, #e67e22, #e74c3c);
  font-family: "Open Sans", serif; }

.app {
  height: 90vh;
  box-shadow: 0px 13px 40px -13px rgba(0, 0, 0, 0.75); }
  .app__wrapper {
    height: 100%; }
  .app__left-block {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    color: #000;
    text-align: center;
    background: url(/weather/static/media/bg.b6796c73.jpg) center center no-repeat;
    background-size: cover; }
  .app__right-block {
    height: 100%;
    padding-top: 100px;
    padding-left: 50px;
    background-color: #2c3e50; }

@media only screen and (max-width: 750px) {
  .app {
    height: 100vh; }
    .app__left-block {
      height: 30%; }
    .app__right-block {
      height: 70%;
      padding-top: 50px; } }

